import React from 'react'
import './index.less'
import {baseUrl} from "../../../../../config";
import store from './store'
import {observer} from "mobx-react";
import WorkCell from "../../../../../cpns/WorkListCell";

@observer
class TaskAceptedCell extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs: [],
            contentModal: '',
            visibleCommunications: false,
            worksIng: [],
            worksExpired: [],
        };
        // store state
        store.fetchWorks();
        // 
        store.setTPL(this.props.tpl);
        // 
        store.setSort(this.props.sort);
    }
    renderMyWorks = ()=>{
        const ws = store.worksByTpl;
        const cpns = [];
        for(const key in ws){
            const p = ws[key];
            cpns.push(
                <WorkCell workInfo={p}/>
            )
        }
        return cpns
    };
    getCommunications = (id) => {
        const ws = store.worksByTpl;
        for (const w in  ws) {
            const p = ws[w];
            // console.log(p);
            if (p.id === id) {
                return (
                    <div>
                        <h2 className='phone'>:{p.communications.phone}</h2>
                        <img src={`${baseUrl}/${p.communications.wx_png}`} alt=''
                             className='avatar'
                             width='100%'
                        />
                        <h5></h5>
                    </div>
                )
            }
        }
    };
    showCommunications = (id) => {
        console.log(id);
        this.setState({
            visibleCommunications: true,
            contentModal: this.getCommunications(id),
        });
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    type_convert = (type) => {
        switch (type) {
            case 1:
                return "";
            case 2:
                return "";
            case 3:
                return "";
            case 4:
                return "";
            case 0:
                return "";
            default:
                return "";
        }
    };
    render() {
        return (
                <div className='my-works-accepted'>
                    {this.renderMyWorks()}
                    {/**/}
                </div>
        )

    }
}
export default TaskAceptedCell;