import React from 'react'
import './index.less'
import {Button, Icon, message, notification} from 'antd';
import axios from "axios";
import {baseUrl} from "../../config";
import {CopyToClipboard} from "react-copy-to-clipboard";
import {Link} from "react-router-dom";
import wxQR from './wxQR.jpg';

const staticUrl = baseUrl;


export default class Withdraw extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            idForCopy:0,
        };
        console.log(staticUrl);
    }
    componentDidMount() {
        axios.get(
            baseUrl + '/api/ti_xian',
            {
                params: {
                    token: localStorage.customer_token,
                }
            })
            .then((Response) => {
                this.setState({
                    idForCopy: Response.data.id,
                });
            });
    };
    infoCopied = () => {
        message.success('');
    };
    render() {
        return (
            <div>
                <h1></h1>
                <p>
                    <br/>
                    1.1000(10)
                    <br/>
                    2.3%1000 9.7
                    <br/>
                    3.id
                </p>
                    {
                        this.state.idForCopy!==undefined?
                        <div>
                            <span>
                                ID:{this.state.idForCopy}
                            </span>
                            < CopyToClipboard text={this.state.idForCopy}
                                              onCopy={this.onCopy}>
                                <Button className='button-copy' onClick={this.infoCopied}>
                                    
                                </Button>
                            </CopyToClipboard>
                        </div>
                            :<span style={{color:'red'}}></span>
                    }
                <img src={wxQR} alt="" width='100%'/>
            </div>
        )
    }
}